.price {
  font-size: 1.6rem;
  font-weight: 700;
}

.price-total {
  float: right;
}
.cart-item-wrap {
  padding-left: 0;
  max-height: 20rem;
  overflow: auto;
}
.buttonOrder {
  padding-top: 2rem;
  float: right;
}

.button-active {
  background-color: #8a2b06;
  color: #fff;
  border: none;
  margin-left: 1rem;
  padding: 1rem 1.3rem;
  border-radius: 2rem;
  cursor: pointer;
}

.button-active:hover {
  background-color: #6b1e00;
}

.success p {
  text-align: center;
  font-size: 1.6rem;
  font-weight: 600;
}
.success button {
  float: right;
}
