.form-item {
  display: flex;
  justify-content: space-between;
  margin-top: 3rem;
}
.form-item label {
  font-size: 1.6rem;
  color: #8a2b06;
  font-weight: 600;
  margin-right: 1rem;
}

.form-item input {
  width: 4rem;
  margin-right: 1rem;
}
.form-item button {
  width: 5rem;
  height: 3rem;
  cursor: pointer;
  background-color: #8a2b06;
  color: #fff;
  border: none;
  border-radius: 10px;
}

.form-item button:hover {
  background-color: #581d06;
}
