.item-cart {
  list-style: none;
  font-size: 1.6rem;

  padding-bottom: 5px;
  border-bottom: 1px solid #8a2b06;
}

.infoItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.infoItem button {
  padding: 0.5rem 2rem;
  margin-left: 0.5rem;
  background-color: #c24412;
  border: none;
  color: #fff;
  border-radius: 1rem;
  cursor: pointer;
}

.infoItem button:hover {
  background-color: #722e12;
}

.moreDetails {
  font-size: 1.6rem;
  color: #722e12;
  font-weight: 700;
}

.quantityDetails {
  float: right;
}
