.header {
  background-color: #8a2b06;
  color: #fff;
  position: fixed;
  width: 100%;
  display: flex;
  font-size: 1.6rem;
  max-height: 7rem;
  justify-content: space-around;
  z-index: 10;
}

.headerTitle {
  display: inline-flex;
}
.headerTitle h3 {
  font-size: 2.2rem;
  font-family: "Dancing Script", cursive;
}

.headerTitle ul {
  font-family: "Roboto", sans-serif;
  display: flex;
  font-weight: 500;
}

.headerTitle li {
  list-style: none;
  /* padding: 0 2rem; */
  cursor: pointer;
  position: relative;
  height: 70px;
  top: -16px;
}

.headerTitle li a {
  text-decoration: none;
  color: #fff;
  display: inline-block;
  padding: 0 20px;
  /* top: 1rem; */
  line-height: 70px;
}

.headerTitle li a:hover {
  background-color: #5e2008;
}
.headerImg {
  width: 100%;
  height: 62rem;
  overflow: hidden;
  object-fit: contain;
  z-index: 0;
}

@media (max-width: 768px) {
  .headerImg {
    height: 50rem;
  }
  .headerTitle ul {
    display: none;
  }
}
