.cart {
  background-color: #4d1601;
  width: 20rem;
  height: 5rem;
  border-radius: 5rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
  text-align: center;
  cursor: pointer;
  border: none;
  color: #fff;
  margin-top: 1rem;
  font-weight: 600;
}
.cart:hover {
  background-color: #612d16;
}
.quantity:hover {
  background-color: #92320c;
}
.quantity {
  padding: 0.75rem 1rem;
  background-color: #b94517;
  border-radius: 50%;
}

.bump {
  animation: bump 300ms ease-out;
}

@keyframes bump {
  0% {
    transform: scale(1);
  }
  10% {
    transform: scale(0.9);
  }
  30% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1.15);
  }
  100% {
    transform: scale(1);
  }
}
