.footer {
  background-color: #4f1a05;
  color: #fff;
  font-size: 1.6rem;
  display: flex;
  justify-content: space-around;
}

.footer-right h4 {
  font-style: italic;
}
.footer-right p {
  float: right;
}

@media (max-width: 768px) {
  .footer {
    display: block;
    padding: 2rem 0 5rem 1rem;
  }
  .footer-right p {
    position: relative;
    left: -3rem;
  }
}
